#block-cmsinfo {
  border-radius: 2px;
  margin: em(70px) 0;
  padding: em(50px) em(290px);
  text-align: center;
  h3 {
    text-transform: uppercase;
    color: $gray-darker;
    font-size: em(25px);
    font-weight: 700;
  }
  p {
    color: $gray-darker;
    font-weight: 400;
    font-size: 1.1em;
    .dark {
      color: $gray;
      font-weight: 400;
    }
  }
}
@include media-breakpoint-down(md) {
  #block-cmsinfo {
    padding: $medium-space $large-space;
  }
}



